import { Component, OnInit, Inject, Optional, ViewEncapsulation } from '@angular/core';

import { MatDialogRef } from "@angular/material/dialog";
import { MAT_DIALOG_DATA } from '@angular/material/dialog';

import { LearnAnimations } from 'src/@learn/animations';

import { ModalsService } from '../modals.service';

@Component({
  selector: 'app-response-modal',
  templateUrl: './response-modal.component.html',
  styleUrls: ['./response-modal.component.scss'],
  encapsulation  : ViewEncapsulation.None,
  animations   : LearnAnimations
})
export class ResponseModalComponent implements OnInit {

  message:any;
  content = '';

  constructor(
    @Optional() @Inject(MAT_DIALOG_DATA) public data: any,
    private confirmDialogRef: MatDialogRef<ResponseModalComponent>,
    private modalsService:ModalsService
  ) {

    this.message = data

  }

  close() {
    this.confirmDialogRef.close(true);
  }

  ngOnInit(): void {



  }

}
